@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --main-bg: #010B13;
    --text-color: white;
}

body {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.App {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    
    width: 100vw;
    height: 100vh;

    background-color: #010B13;

    background-color: var(--main-bg);
    color: white;
    color: var(--text-color);
    font-family: 'Montserrat', sans-serif;
    overflow: hidden;
}
.quiz {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;

    height: 100%;
    width: 100%;
}

.timer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    background-color: #adff2f;
    position: absolute;
    top: 0px;
    left: 0px;
    height: 0.5vmin;
    width: 100%;
    transition: width ease-in;
}

.question {
    font-weight: 500;
    margin: 2rem;
}

.choices {
    display: -webkit-flex;
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 2rem;
}

.choice, .disabled {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    display: -webkit-flex;
    display: flex;
    padding: 1rem;
    background-color: transparent;
    color: white;
    border: 1px solid white;
    font-weight: bold;
}

.choice:hover, .correct  {
    background-color: #adff2f;
    color: #252850;
}

.mistaken {
    background-color: firebrick;
}
table, th, td {
    border: 3px solid white;
    border-collapse: collapse;
}

table .answers {
    text-align: center;
    font-weight: bold;
}

th, td {
    padding: 3vmin;
}

.results {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100%;
    width: 100%;
    grid-gap: 5vmin;
    gap: 5vmin;
    overflow-y: scroll;
    padding: 5vmin;
}

.title {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}
